import React, { useEffect, useRef } from "react";
import Header from "../../components/html-page/header/header";
import Main from "../../components/html-page/main/main";
import PageChangebButton from "../../ui/page-changer-btn/page-changer-right-btn";

const HtmlPage = () => {
  const myRef = useRef(null);
  useEffect(() => {
    window.addEventListener("scroll", scrollHandler);
    return () => {
      window.removeEventListener("scroll", scrollHandler);
    };
  }, []);
  const scrollHandler = () => {
    myRef.current.childNodes[0].style.opacity =
      myRef.current.scrollWidth > 1050 ? 1 - window.pageYOffset / 1200 + "" : 1;
    myRef.current.childNodes[1].style.opacity =
      myRef.current.scrollWidth > 1050
        ? -0.5 + window.pageYOffset / 500 + ""
        : 1;
  };
  return (
    <>
      <PageChangebButton pageLink="/js-page" buttonStyle="dev" />
      <div ref={myRef} className="html_page-wrapper">
        <Header />
        <Main />
      </div>
    </>
  );
};

export default HtmlPage;
