import React from "react";
import { NavLink } from "react-router-dom";
import SkillsAnimation from "../../ui/skills-animation/skills-animation";
import "./welcome.css";

const Welcome = () => {
  const icons = ["skill_icon-html", "skill_icon-css", "skill_icon-js"];
  return (
    <div className="welcome-wrapper">
      <SkillsAnimation icons={icons} />
      <div className="container">
        <div className="pages">
          <NavLink className="link-container-url" to="/js-page">
            <div className="link-container js-page">
              <div className="content">
                <h2>Developer-mode</h2>
                <p>Look at this portfolio with my eyes</p>
              </div>
            </div>
          </NavLink>
          <NavLink className="link-container-url" to="/html-page">
            <div className="link-container user-page">
              <div className="content">
                <h2>User-mode</h2>
                <p>Look at this portfolio with simpl user eyes</p>
              </div>
            </div>
          </NavLink>
        </div>
      </div>
    </div>
  );
};

export default Welcome;
