import React from "react";
import { Route, Switch } from "react-router-dom";
import ErrorPage from "../error-page/error-page";
import Welcome from "../../../pages/welcome/welcome";
import JsPage from "../../../pages/js-page/js-page";
import HtmlPage from "../../../pages/html-page/html-page";

import { fetchReactPageData } from "../fake-api/fakeApi";

const resource = fetchReactPageData();
const PageStarter = () => {
  const reactPageData = resource.reactPageData.read();
  return (
    <Switch>
      <Route exact path="/" component={Welcome} />
      <Route path="/html-page" component={HtmlPage} />
      <Route
        path="/js-page"
        component={() => <JsPage data={reactPageData.data} />}
      />
      <Route component={ErrorPage} />
    </Switch>
  );
};

export default PageStarter;
